<template>
  <div>
      <Top :topList="topList"/>
      <Divide :title="first"/> 
      <FirstList :firstList="firstList"/>     
      <Divide :title="second"/>
      <SecondList :secondList="secondList"/>    
      <Divide :title="category"/> 
      <CategoryList :categoryList="categoryList"/>
      <Divide :title="third"/>
      <ThirdList2 :thirdList="thirdList" :addThirdList="addThirdList"/>                                        
  </div>
</template>

<script>
  import Top from '@/pages/home/Top.vue'
  import Divide from '@/pages/home/Divide.vue'
  import FirstList from '@/pages/home/FirstList.vue'
  import SecondList from '@/pages/home/SecondList.vue'
  import CategoryList from '@/pages/home/CategoryList.vue'
  import ThirdList2 from '@/pages/home/ThirdList2.vue'

  export default {
    components: {
      Top,
      Divide,
      FirstList,
      SecondList,
      CategoryList,
      ThirdList2           
    },    
    data () {
      return {
        first:'今日热门',
        second:'本周上新',
        category:'精选专题',
        third:'热门产品',
        topList: [],
        categoryList: [],  
        firstList: [],
        secondList: [],  
        thirdList: []                                     
      }
    },
    created() {
      this.getAllBanner()
      this.getAllProduct()
    },        
    methods: {
      getAllBanner() {
        this.$axios.get('getAllBanner',{

        })
        .then(res=>{
          
          if (res.data.code === 200) {
              // console.log('--------------------------')
              // console.log(res.data)
              // console.log(res.data.data.top) 

              // 获取轮播图数据
              // this.topList = res.data.data.top // 因未过滤从后台接收的数据，不推荐使用。
              this.topList = res.data.data.top.map(item => ({
                src: item.src,
                link: item.link
              })) 

              // 获取精选专题数据
              // this.categoryList = res.data.data.category // 因未过滤从后台接收的数据，不推荐使用
              this.categoryList = res.data.data.category.map(item => ({
                src: item.src,
                link: item.link
              }))               
          } else {
              console.log('**********code is not 200 ***********')
          }

        })
        .catch(err=>{
            console.log(err)
        })

      },
      getAllProduct() {
        this.$axios.get('getAllProduct',{

        })
        .then(res=>{
          if (res.data.code === 200) {
            ///////////////////////////////////////////////////////////////////////////////                
            // 处理 '今日热门' 数组 
            ///////////////////////////////////////////////////////////////////////////////                
            var firstListTemp = []
            firstListTemp = res.data.data.first  

            for (var i = 0; i < firstListTemp.length; i++) {                   
              firstListTemp[i].success_num /=  10000 
              firstListTemp[i].success_num = firstListTemp[i].success_num.toFixed(1) + '万人申请'
            }  

            this.firstList = firstListTemp

            ///////////////////////////////////////////////////////////////////////////////                
            // 处理 '本周上新' 数组 
            ///////////////////////////////////////////////////////////////////////////////
            var secondListTemp = []
            secondListTemp = res.data.data.second   

            for (var i = 0; i < secondListTemp.length; i++) {                   
              secondListTemp[i].success_num /=  10000 
              secondListTemp[i].success_num = secondListTemp[i].success_num.toFixed(1) + '万人申请'
            }  

            this.secondList = secondListTemp
            ///////////////////////////////////////////////////////////////////////////////                
            // 处理 '热门产品' 数组 
            /////////////////////////////////////////////////////////////////////////////// 
            this.thirdList = res.data.data.third
          } else {
            console.log('**********code is not 200 ***********')
          }

        })
        .catch(err=>{
          console.log(err)
        })

      },
      addThirdList(arr) {
        this.thirdList = this.thirdList.concat(arr)
      }
    }
  }
</script>


<style  lang="scss" scoped>

</style>



